@import url(https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,400;1,700&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --main: #e8b239;
}
.button-default {
  color: white;
  background-color: #e8b239;
  background-color: var(--main);
  text-align: center;
  text-transform: uppercase;
  padding: 5px 10px;
  display: inline-block;
}

.button-slanted {
  transform: skewX(-20deg);
}

.button-slanted-content {
  transform: skewX(20deg);
  display: inline-block;
}

.formInput {
  background: #2c2c2c;
  border: 2px solid #e8b239;
  border: 2px solid var(--main);
}

.shareProfileLink {
  position: relative;
  margin-top: 20px;
  padding: 10px 0 10px 110px;
  background: linear-gradient(
    to right,
    transparent 0%,
    transparent 30%,
    #e8b239 30%,
    #e8b239 60%
  );
  -webkit-clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
          clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
}

.shareLinksUrl a .icon {
  font-size: 1.2rem;
  color: #ffffff;
}

.shareLinksUrl li {
  padding-left: 1rem;
}

.shareLinksUrl a {
  background: #2c2c2c;
  padding: 11px 11px;
  border-radius: 19px;
}

.titlePort-imb {
  color: white;
  font-size: 16px;
}

.shareLinksUrl {
  margin-left: 60px;
  margin-bottom: 21px;
}

.row.shareLinks {
  margin-bottom: 4rem;
}

@media only screen and (max-width: 600px) {
  ul.ReactLinkUl.d-md-flex.shareLinksUrl {
    display: flex;
    margin-left: 1rem;
  }
}

