:root {
  --main: #e8b239;
}
.button-default {
  color: white;
  background-color: var(--main);
  text-align: center;
  text-transform: uppercase;
  padding: 5px 10px;
  display: inline-block;
}

.button-slanted {
  -ms-transform: skewX(-20deg);
  -webkit-transform: skewX(-20deg);
  transform: skewX(-20deg);
}

.button-slanted-content {
  -ms-transform: skewX(20deg);
  -webkit-transform: skewX(20deg);
  transform: skewX(20deg);
  display: inline-block;
}

.formInput {
  background: #2c2c2c;
  border: 2px solid var(--main);
}

.shareProfileLink {
  position: relative;
  margin-top: 20px;
  padding: 10px 0 10px 110px;
  background: linear-gradient(
    to right,
    transparent 0%,
    transparent 30%,
    #e8b239 30%,
    #e8b239 60%
  );
  clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
}

.shareLinksUrl a .icon {
  font-size: 1.2rem;
  color: #ffffff;
}

.shareLinksUrl li {
  padding-left: 1rem;
}

.shareLinksUrl a {
  background: #2c2c2c;
  padding: 11px 11px;
  border-radius: 19px;
}

.titlePort-imb {
  color: white;
  font-size: 16px;
}

.shareLinksUrl {
  margin-left: 60px;
  margin-bottom: 21px;
}

.row.shareLinks {
  margin-bottom: 4rem;
}

@media only screen and (max-width: 600px) {
  ul.ReactLinkUl.d-md-flex.shareLinksUrl {
    display: flex;
    margin-left: 1rem;
  }
}
